import Swal from 'sweetalert2';

const modal = document.querySelector('[data-order-modal]');
const closeBtn = document.querySelector('[data-order-close]');
const backdrop = document.querySelector('[data-backdrop]');
const form = document.querySelector('[data-order-form]');
const openBtn = document.querySelector('.modalpet-adopt-btn');
const petModal = document.querySelector('.modalpet-backdrop');

/* ================= OPEN / CLOSE ================= */

function openModal() {
  modal.classList.remove('is-hidden');
  document.body.style.overflow = 'hidden';
}

function closeModal() {
  modal.classList.add('is-hidden');
  document.body.style.overflow = '';
}

openBtn?.addEventListener('click', () => {
  openModal();
  petModal?.classList.add('is-hidden');
});

closeBtn?.addEventListener('click', closeModal);

backdrop?.addEventListener('click', e => {
  if (e.target === backdrop) closeModal();
});

document.addEventListener('keydown', e => {
  if (e.key === 'Escape' && !modal.classList.contains('is-hidden')) {
    closeModal();
  }
});

/* ================= VALIDATION HELPERS ================= */

function showError(input, message) {
  const errorEl = input.parentElement.querySelector('.order-form__error');
  input.classList.add('is-error');
  errorEl.textContent = message;
}

function clearError(input) {
  const errorEl = input.parentElement.querySelector('.order-form__error');
  input.classList.remove('is-error');
  errorEl.textContent = '';
}

/* ================= SUBMIT (FRONTEND ONLY) ================= */

form?.addEventListener('submit', e => {
  e.preventDefault(); // üî¥ –∫—Ä–∏—Ç–∏—á–Ω–æ

  const nameInput = form.elements.name;
  const phoneInput = form.elements.phone;
  const comment = form.elements.comment.value.trim();

  let isValid = true;

  clearError(nameInput);
  clearError(phoneInput);

  /* Name validation */
  if (nameInput.value.trim().length < 2) {
    showError(nameInput, '–Ü–º º—è –ø–æ–≤–∏–Ω–Ω–æ –º—ñ—Å—Ç–∏—Ç–∏ –º—ñ–Ω—ñ–º—É–º 2 —Å–∏–º–≤–æ–ª–∏');
    isValid = false;
  }

  /* Phone validation */
  const phonePattern =
    /^\+38\s?\(?0\d{2}\)?\s?\d{3}\s?\d{2}\s?\d{2}$/;

  if (!phonePattern.test(phoneInput.value.trim())) {
    showError(phoneInput, '–ù–µ–≤—ñ—Ä–Ω–∏–π —Ñ–æ—Ä–º–∞—Ç –Ω–æ–º–µ—Ä–∞');
    isValid = false;
  }

  if (!isValid) return;

  /* –Ü–ú–Ü–¢–ê–¶–Ü–Ø –í–Ü–î–ü–†–ê–í–ö–ò */
  Swal.fire({
    icon: 'success',
    title: '–ó–∞—è–≤–∫—É –Ω–∞–¥—ñ—Å–ª–∞–Ω–æ!',
    text: '–ú–∏ –∑–≤ º—è–∂–µ–º–æ—Å—è –∑ –≤–∞–º–∏ –Ω–∞–π–±–ª–∏–∂—á–∏–º —á–∞—Å–æ–º',
    confirmButtonText: '–î–æ–±—Ä–µ',
  });

  form.reset();
  clearError(nameInput);
  clearError(phoneInput);
  closeModal();
});
