const burgerBtn = document.querySelector('.burger-btn');
const mobileMenu = document.getElementById('mobile-menu');
const closeBtn = document.getElementById('data-burger-close');
const menuLinks = document.querySelectorAll('.menu-link-menu');

// –≤—ñ–¥–∫—Ä–∏—Ç–∏ –º–µ–Ω—é
burgerBtn.addEventListener('click', () => {
  mobileMenu.classList.add('open');
});

// –∑–∞–∫—Ä–∏—Ç–∏ –º–µ–Ω—é
closeBtn.addEventListener('click', () => {
  mobileMenu.classList.remove('open');
});

// –∑–∞–∫—Ä–∏—Ç—Ç—è –ø—Ä–∏ –∫–ª—ñ–∫—É –ø–æ–∑–∞ –º–µ–Ω—é
mobileMenu.addEventListener('click', (e) => {
  if (e.target === mobileMenu) {
    mobileMenu.classList.remove('open');
  }
});
// –∑–∞–∫—Ä–∏—Ç—Ç—è –ø–æ –∫–ª–∞–≤—ñ—à—ñ ESC
document.addEventListener('keydown', (e) => {
  if (e.key === 'Escape' && mobileMenu.classList.contains('open')) {
    mobileMenu.classList.remove('open');
  }
});

// –∑–∞–∫—Ä–∏—Ç—Ç—è –º–µ–Ω—é –ø—ñ—Å–ª—è –∫–ª—ñ–∫—É –Ω–∞ –ø–æ—Å–∏–ª–∞–Ω–Ω—è
menuLinks.forEach(link => {
  link.addEventListener('click', () => {
    mobileMenu.classList.remove('open');
  });
});